import React, { lazy, Suspense } from "react";
import "./App.scss";
import { setDefaultUrl } from "./assets/utills/axios";
import { Provider } from "react-redux";
import store from "./redux/store";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useCookies } from "react-cookie";
import { Spinner } from "react-bootstrap";
import SignInSignUpPage from "./pages/signin-signup-page/signin-signup-page";

const TablePage = lazy(() => import("./pages/table-page/table-page"));
const HomePage = lazy(() => import("./pages/home-page/home.component"));
// const ConfirmUser = lazy(() => import("./pages/confirm-user/confirm-user"));
const UsersTablePage = lazy(
  () => import("./pages/users-table-page/users-table-page")
);

function App() {
  const [cookies] = useCookies(["userdata"]);
  setDefaultUrl();

  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#0B2949",
      },
      secondary: {
        main: "#DC3545",
      },
    },
  });
  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter>
        <Suspense fallback={<Spinner animation="border" />}>
          <Switch>
            <Provider store={store}>
              <Route exact path="/" component={HomePage} />
              {/* <Route path="/confirm/user/:code" component={ConfirmUser} /> */}
              <Route path="/account" component={SignInSignUpPage} />
              <Route
                exact
                path="/table"
                render={() =>
                  !cookies["userdata"] ? <Redirect to="/" /> : <TablePage />
                }
              />
              <Route
                exact
                path="/users-table"
                render={() =>
                  cookies["userdata"]?.name !== "admin" ? (
                    <Redirect to="/" />
                  ) : (
                    <UsersTablePage />
                  )
                }
              />
              {/* <Route>
                <Redirect to="/" />
              </Route> */}
            </Provider>
          </Switch>
        </Suspense>
        <ToastContainer rtl />
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
