import React, { lazy, Suspense } from "react";

import "./signin-signup-page.scss";
import { Col, Row, Spinner } from "react-bootstrap";
import Logo from "../../assets/image/Logo.png";
import { Switch, Route } from "react-router-dom";

const SignIn = lazy(() => import("../../components/SignIn/SignIn"));
const ForgetPassword = lazy(
  () => import("../../components/forget-password/forget-password-form")
);
const SignUp = lazy(() => import("../../components/SignUp/SignUp"));
const ConfirmUser = lazy(() => import("./../confirm-user/confirm-user"));

function SignInSignUpPage({ match: { path, url } }: any) {
  return (
    // <Container noGutters fluid >
    <Row noGutters className="Sign-in-sign-up">
      <Col xs={12} sm={4} md={4} className="Sign-in-sign-up-inputs-box-gr">
        <div className="Sign-in-sign-up-form">
          <img src={Logo} alt="Logo" className="signin-signup-logo" />
          <Suspense fallback={<Spinner animation="border" />}>
            <Switch>
              <Route path={`${path}/signin`} component={SignIn} />
              <Route path={`${path}/signup`} component={SignUp} />
              <Route
                path={`${path}/forgetpassword`}
                component={ForgetPassword}
              />
              <Route
                path={`${path}/confirm/user/:id/:code`}
                component={ConfirmUser}
              />
            </Switch>
          </Suspense>
          {/* {currentForm === "login" ? (
            <SignIn
              goToSignUp={() => setCurrentForm("signup")}
              goToForgetPassword={() => setCurrentForm("forgetpassword")}
            />
          ) : currentForm === "signup" ? (
            <SignUp goToLogin={() => setCurrentForm("login")} />
          ) : (
            <ForgetPassword goToLogin={() => setCurrentForm("login")} />
          )} */}
        </div>
      </Col>
      <Col xs={0} sm={8} md={8} className="Sign-in-sign-up-bg">
        {/* <AhmedsPhotoAndDetails /> */}
      </Col>
    </Row>
    // </Container>
  );
}

export default SignInSignUpPage;
