const INITIAL_STATE = {
  query: "",
  selectedPage: 1,
  showDelete: false,
  showEdit: false,
  editContributorData: {},
  tableContent: [],
  showAdd: false,
  genderFilter: null,
};

const tableReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case "SET_QUERY":
      return {
        ...state,
        query: action.payload,
      };
    case "SET_SELECTED_PAGE":
      return {
        ...state,
        selectedPage: action.payload,
      };
    case "SET_SHOW_DELETE":
      return {
        ...state,
        showDelete: action.payload,
      };
    case "SET_SHOW_EDIT":
      return {
        ...state,
        showEdit: action.payload,
      };
    case "SET_SHOW_ADD":
      return {
        ...state,
        showAdd: action.payload,
      };
    case "RESET_EDIT_CONTRIBUTOR_DATA":
      return {
        ...state,
        editContributorData: {},
      };
    case "SET_EDIT_CONTRIBUTOR_DATA":
      return {
        ...state,
        editContributorData: action.payload,
      };
    case "SET_TABLE_CONTENT":
      return {
        ...state,
        tableContent: action.payload,
      };
    case "SET_GENDER_FILTER":
      return {
        ...state,
        genderFilter: action.payload,
      };
    default:
      return state;
  }
};

export default tableReducer;
